@use 'variaveis';

body {
    background-color: variaveis.$corPrincipal;
}

.formContainer {
    max-width: 150px;
    padding: 20px;
    background-color: #f0f0f0;
    border-radius: 10px;
    text-align: center;
    align-items: center;
}

button{
    margin-top: 10px;
}

.imagem {
    width: 200px;
    height: 200px;
    margin: 5px;
    align-items: center;
}

.imagem-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 200px;
}
